* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  background: #1b1b1b;
  overflow-x: hidden;
}

h2 {
  font-weight: normal;
  font-size: 4rem;
}

h3 {
  color: white;
}

h4 {
  font-weight: bold;
  font-size: 2rem;
}

a {
  font-size: 1.1rem;
}

span {
  font-weight: bold;
  color: #23d997;
}

p {
  padding: 3rem 0rem;
  color: #ccc;
  font-size: 1.4rem;
  line-height: 150%;
}

button {
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 3px solid #23d997;
  background: transparent;
  color: white;
  transition: all 0.5s ease;
  font-family: "Inter", sans-serif;
  &:hover {
    background-color: #23d997;
    color: white;
  }
}

@media screen and (max-width: 1700px) {
  html {
    font-size: 75%;
  }
}

@import "./nav";

@import "./aboutSection";
@import "./wave";
@import "./servicesSection";
@import "./faqSection";

@import "./ourWork";
@import "./movieDetails";

@import "./contactUs";
